.popup {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,.8);
}

.content {
    background-color: #141414;
    padding: 30px;
    gap: 30px;
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    width: 90%;
    max-width: 340px;
    box-sizing: border-box;
    position: relative;
}

.title {
    color: white;
    text-align: center;
}

.close {
    width: 30px;
    height: 30px;
    top: 6px;
    right: 6px;
    position: absolute;
    background-color: transparent;
    border: none;
    outline: none;
    background: url('../../assets/close-icon.svg') center no-repeat;
}
.close:hover {
    cursor: pointer;
}